import { PropsWithChildren } from 'react'
import './message.css'
import { Modal } from 'antd'

type PropsModal = {
    isOpen: boolean,
    close: () => void,
    submit: () => void,
}


export const Message: React.FC<PropsWithChildren<PropsModal>> = (props: PropsWithChildren<PropsModal>) => {
    return (
        <div >
            <Modal
                className='modalBoby'
                open={props.isOpen}
                onCancel={props.close}
                onOk={props.submit}
                okText="Звучит интересно"
                styles={{
                    mask: {
                        backdropFilter: 'blur(10px)',
                    },
                }}
            >
                <p>
                    Дорогая Оля,<br />

                    Твои глаза – два океана, полные чудес,<br />
                    И в них я тону, как в море нежных роз.<br />
                    Твоя улыбка – солнце, что согревает душу,<br />
                    Твои руки – уют, в котором я нашла покой.<br />
                    Ты – моя мечта, моя нежность, моя страсть, <br />
                    С тобой каждый момент – волшебство и восторг. <br />
                    Пусть этот день будет полон цветов и сказки, <br />
                    Пусть наши сердца бьются в унисон, как в романе.
                    <br /><br />
                    Приглашаю тебя на романтичный ужин вместе с Мэном(мной)!!!<br />
                </p>
            </Modal>

        </div>
    )
}