import './dish.css'
import { PropsWithChildren } from 'react'



type DishProps = {
    description: string,
    title: string,
    name: string,
    icon: string,
}


export const Dish: React.FC<PropsWithChildren<DishProps>> = (props: PropsWithChildren<DishProps>) => {


    return (
        <div>
            <div className="dish-containers-corusel">
                <img className='image-corusel' alt='f' title='fdf' src={props.icon} />
                <div className='middle-corusel'>
                    <div className="text-corusel">
                        <p >{props.name}</p>
                        <p >{props.title}</p>
                        <p >{props.description}</p>
                    </div>
                    {/* <div className="centered-title">{props.title}</div>
            <div className="centered-description">{props.description}</div> */}
                </div>
            </div>
        </div>
    )
}